	module.exports = {"data":{"markdownRemark":{"html":"<p><em>(Cet article est le 4<sup>e</sup> d'une série sur l'<a href=\"../histoire-de-la-silicon-valley-1/\">histoire de la Silicon Valley</a>.)</em></p></p>\n<p>\n<a href=\"http://www.flickr.com/photos/samkinsley/2651255287/\" title=\"Palo Alto Research Center (samkinsley on Flickr)\"><img src=\"http://farm4.static.flickr.com/3072/2651255287_8395819dab_m.jpg\" width=\"240\" height=\"180\" alt=\"Palo Alto Research Center\" style=\"float: right; margin: 0 0.5em 2em 1em; box-shadow: 1px 1px 2px #777\"></a>\nEn 1969, <b>Xerox</b> est une société florissante, grâce au développement du marché des photocopieurs.</p>\n<p>Loin de se reposer sur ses lauriers, le PDG voulait anticiper les évolutions des besoins de ses clients et donc de son métier.</p>\n<p>Pour donner corps à sa vision, il demande au responsable de la R&D de créer un nouveau centre de recherche, qui sera installé à <b>Palo Alto</b>.</p>\n<p style=\"clear: both\">En 1970, le gouvernement diminuait les crédits de recherche dirigés vers l’informatique, ce qui a permis à Xerox de recruter bon nombre des meilleurs chercheurs du domaine.</p>\n<p>Pendant trente ans, ce laboratoire de recherche privé sera à l’origine de nombreuses innovations. Certaines seront exploitées par Xerox dans ses produits (imprimantes laser, copieurs), d’autres seront adoptées ailleurs (l’interface graphique chez Apple).</p>\n<p style=\"clear:both\"><em><a href=\"../histoire-de-la-silicon-valley-5/\">Lire le 5<sup>e</sup> épisode…</a></em></p>\n<p style=\"clear:both; text-align: right\">\n<em>Crédits photo : <a href=\"http://www.flickr.com/people/samkinsley/\">samkinsley</a></em></p>","frontmatter":{"title":"Xerox et le PARC","date":"2011-03-17","tags":["Silicon-Valley"]}}},"pathContext":{"slug":"/articles/histoire-de-la-silicon-valley-4/"}}

